export default {
   name: 'FooterComponent',
   components: {},
   data: () => ({
      title: '',
      show: true,
      copyright: '    ',
      description: '                ,                                          ',
      socials: [{
         icon: 'fab fa-facebook',
         url: 'https://www.facebook.com/groups/ProgrammersUnion/'
      }, {
         icon: 'fab fa-twitter',
         url: ''
      }, {
         icon: 'fab fa-google-plus',
         url: ''
      }, {
         icon: 'forum',
         url: 'https://forums.coretabs.net/'
      }]
   }),
   created() {
      this.title = this.$store.state.title
      this.setFooter()
   },
   watch: {
      $route(to, from) {
         this.setFooter()
      }
   },
   methods: {
      setFooter() {
         switch (this.$route.name) {
            case '404':
            case 'course':
            case 'courses':
               this.show = false
               break
            default:
               this.show = true
         }
      }
   }
}



